.govuk-grid-column-two-thirds {
  position: relative; }

/* [data-block-id] */
main.govuk-main-wrapper {
  position: relative; }

.fb-hover {
  background-color: #fc9;
  background-color: rgba(255, 204, 153, 0.25);
  background-color: rgba(44, 141, 191, 0.125); }

.fb-block {
  position: relative; }

.fb-block-edit-container {
  position: absolute;
  margin-left: -2.5rem;
  margin-top: 0;
  width: 4rem;
  height: 4rem;
  z-index: 99999999; }

.govuk-inset-text .fb-block-edit-container {
  margin-left: -4rem; }

.govuk-warning-text .fb-block-edit-container {
  margin-top: -1rem;
  top: 50%; }

.fb-block-instance-properties {
  position: absolute;
  z-index: 99999999;
  margin-left: 0;
  left: -4rem;
  background: #fff; }

.fb-mode-edit > .fb-block-instance-properties {
  margin-top: -1.5rem; }

.govuk-radios__conditional .fb-block-instance-properties {
  margin-left: -3.5rem; }

.fb-block-instance-properties:hover {
  z-index: 999999999; }

.fb-block-instance-properties-heading {
  position: absolute;
  top: 0rem;
  left: -1rem; }

.fb-mode-edit .fb-block-instance-properties ul {
  margin: 0;
  padding: 0;
  display: none;
  box-shadow: 1px 1px 0 #005ea5, -1px 1px 0 #005ea5, -1px -1px 0 #005ea5, 1px -1px 0 #005ea5; }

.fb-block-instance-properties:hover ul {
  display: block; }

.fb-mode-edit .fb-block-instance-properties li {
  list-style-type: none;
  margin: 0;
  white-space: nowrap;
  padding: 0 0.5rem 0 1rem;
  background: #fff;
  line-height: 1.375; }

.fb-block-instance-properties li + li.fb-block-instance--all {
  margin-top: 1rem; }

.fb-block-instance-properties li.fb-block-instance--delete {
  margin-top: 1rem; }

.fb-block-instance-properties li .fb-block-instance-properties-type,
.fb-block-instance-properties li a,
.fb-block-instance-properties li button {
  text-decoration: none;
  display: block;
  border: none;
  padding: 0.325rem;
  font-size: 1rem;
  cursor: pointer;
  color: #005ea5;
  width: 100%;
  text-align: left;
  background: transparent; }

.fb-block-instance-properties li.fb-block-instance-properties-type-item {
  background: #005ea5; }

.fb-block-instance-properties li .fb-block-instance-properties-type {
  color: #fff; }

.fb-block-instance-properties li:hover {
  background: #005ea5; }

li.fb-block-instance--delete:hover {
  background: #b10e1e; }

.fb-block-instance-properties li:hover a,
.fb-block-instance-properties li:hover button {
  color: #fff; }

.fb-block-instance-properties .fb-block-edit {
  font-size: 1.325rem; }

[data-block-type="fieldset"] > .fb-block-instance-properties,
[data-block-type="section"] > .fb-block-instance-properties,
[data-block-type="group"] > .fb-block-instance-properties {
  margin-top: -2.5rem; }

.fb-mode-edit [data-block-type="section"],
.XXfb-mode-edit :not(.fb-repeatable-item) [data-block-type="fieldset"],
.fb-mode-edit [data-block-type="group"]:not(.fb-repeatable-item) {
  outline: dotted 1px #000;
  padding-bottom: 3rem;
  margin-bottom: -3rem;
  outline-offset: 0.25rem;
  padding-top: 2rem;
  margin-top: -0.75rem;
  margin-right: -1rem;
  position: relative; }

.fb-mode-edit [data-block-type="fieldset"]:before,
.fb-mode-edit [data-block-type="section"]:before,
.fb-mode-edit [data-block-type="group"]:before {
  content: attr(data-block-type);
  position: absolute;
  bottom: 100%;
  margin-bottom: 0.25rem;
  left: -0.25rem;
  margin-left: -2px;
  font-size: 75%;
  text-transform: uppercase;
  background: #999;
  color: #fff;
  padding: 0 0.5rem; }

[data-block-type="fieldsset"] + .fb-editor-button,
[data-block-type="section"] + .fb-editor-button,
[data-block-type="group"] + .fb-editor-button {
  margin-top: 0; }

.govuk-date-input > .fb-block-instance {
  top: 0; }

.govuk-checkboxes__conditional .fb-block-property,
.govuk-radios .fb-block-property {
  margin-left: -6rem; }

.govuk-checkboxes__conditional .fb-block-instance-properties {
  margin-left: -3.5rem; }

.govuk-radios__label > .fb-block-property,
.govuk-checkboxes__label > .fb-block-property {
  margin-top: -0.5rem; }

.govuk-checkboxes__label > .fb-block-property {
  margin-left: -6rem; }

.govuk-radios__hint > .fb-block-property,
.govuk-checkboxes__hint > .fb-block-property {
  margin-top: -0.375rem; }

.govuk-checkboxes__hint > .fb-block-property {
  margin-left: -6rem; }

.fb-block-edit {
  position: absolute;
  font-size: 1.5rem;
  padding: 0;
  cursor: pointer;
  text-decoration: none !important;
  background-color: #fff;
  color: #005ea5 !important;
  border-radius: 50%;
  border: solid 1px #005ea5;
  width: 2rem;
  height: 2rem;
  text-align: center;
  box-sizing: border-box;
  line-height: 1.5;
  font-weight: normal; }

a.fb-block-edit:hover,
.fb-block-instance-properties:hover > .fb-block-edit {
  background-color: #005ea5 !important;
  color: #fff !important; }

details .fb-block-edit {
  margin-left: -1.5rem; }

details .fb-block-instance-properties .fb-block-edit {
  margin-left: 0; }

details .fb-block-instance-properties {
  margin-top: -1.875rem; }

.fb-block-inset .fb-block-instance-properties {
  margin-left: -0.625rem; }

.fb-block-warning .fb-block-instance-properties {
  margin-top: 1rem; }

.fb-flow-wrapper .fb-block-edit {
  visibility: hidden; }

h2 .fb-block-edit,
.fb-flow-wrapper .fb-hover > .fb-block-edit-container > .fb-block-edit {
  visibility: visible; }

.fb-sectionHeading .fb-block-edit-container {
  margin-top: -0.5rem; }

h1 .fb-block-edit-container {
  margin-top: 0; }

h2 .fb-block-edit-container {
  margin-top: 0.25rem; }

.fb-block-instance-property-link {
  position: absolute;
  left: -3000px; }

.govuk-button > .fb-block-edit-container {
  margin-left: -5.25rem;
  margin-top: -0.5rem; }

.fb-repeatable-item--delete.govuk-button > .fb-block-edit-container {
  right: -4.5rem;
  margin-left: 0; }

.fb-repeatable-item {
  border: solid 1px #000;
  box-shadow: 1px 1px 0px 0px #fff, 2px 2px 0px 0px #000, 3px 3px 0px 0px #fff, 4px 4px 0px 0px #000, 5px 5px 0px 0px #fff, 6px 6px 0px 0px #fff, 7px 7px 0px 0px #fff, 8px 8px 0px 0px #fff;
  padding: 5px 0 0 5px;
  margin: -6px 0 2rem -6px; }

.repeatableMinMax {
  position: absolute;
  right: 0;
  top: 100%;
  margin-top: -0.375rem; }

p.fb-group-repeatableSetting {
  display: inline-block;
  margin-left: 4rem;
  margin-bottom: 0;
  font-size: 1rem; }

p.fb-group-add {
  margin-top: -3rem;
  margin-bottom: 1rem; }

legend.govuk-visually-hidden,
label.govuk-visually-hidden {
  position: relative !important;
  width: auto !important;
  height: auto !important;
  overflow: visible !important;
  clip: unset !important;
  --webkit-clip-path: unset !important;
  clip-path: unset !important;
  white-space: normal !important; }

legend.govuk-visually-hidden::before,
label.govuk-visually-hidden::before {
  position: absolute;
  content: 'Question is visually hidden';
  background: #eee;
  color: #000;
  font-size: 0.75rem;
  bottom: 100%; }

.govuk-back-link,
.fb-block-button {
  cursor: default !important;
  user-select: none !important; }
